var time = require('./src/time.js');

var fashionablylate = module.exports = function (inputtedTime) {
  var timeType = time.checkKindOfTime(inputtedTime);
  var convertedTime = time.convertTimeToMins(inputtedTime, timeType);
  var plussedTime = time.timePlusMins(convertedTime, 15);
  var plussedTimeBackForMore = time.convertMinsToTime(plussedTime, timeType);
  return plussedTimeBackForMore;
};